import "./works.scss"
import {useState} from 'react'

export default function Works() {

    const [currentSlide,setCurrentSlide] = useState(0);
    const data = [
        {
            id: "1",

            title: "(PT-BR) Olá, eu sou o Matheus.",
            desc:
              "Estudante de ciencias da computação na Univali(BR) e com alguma experiência profissional em desenvolvimento Web . Além do gosto por programação web, tenho constante curiosidade para resolver e automatizar problemas cotidianos utilizando ferramentas como websrapping e ferramentas de programação de software. Programação à parte, gosto muito de coisas relacionadas com música e ciências de modo geral - principalmente a espacial. Além disso, desde criança aprendi a gostar de aprender, o que me motivou a aprender inglês bem cedo e alemão logo em seguida( apesar de já ter perdido um pouco a prática nessa língua e o contato com ela não ser tão constante quanto o inglês ).  ",
            img:"assets/dev.svg"

          },
          {
            id: "2",
      
            title: "(EN-US) Hi, I'm Matheus.",
            desc:
              "Computer science student at Univali(BR) and with some professional experience in web development. In addition to liking web programming, I am constantly curious to solve and automate everyday problems using tools such as websrapping and software programming tools.Aside from programming, I really like things related to music and science in general - especially space. In addition, since I was a child I learned to enjoy learning, which motivated me to learn English early on and German soon after (although I have already lost a little practice in this language since the contact with it is not as constant as English) .",
            img:"assets/dev2.svg"
          },
          {
            id: "3",
            
            title: "(DE-DE) Hallo, ich bin Matheus.",
            desc:
              "Informatikstudent bei Univali(BR) und mit Berufserfahrung in der Webentwicklung. Neben der Vorliebe für Webprogrammierung bin ich ständig neugierig, alltägliche Probleme mit Tools wie Websrapping und Softwareprogrammierungstools zu lösen und zu automatisieren. Abgesehen von der Programmierung mag ich wirklich Dinge, die mit Musik und Wissenschaft im Allgemeinen zu tun haben – besonders mit dem Weltraum. Außerdem lernte ich seit meiner Kindheit Spaß am Lernen, was mich motivierte, früh Englisch und bald darauf Deutsch zu lernen (obwohl ich in dieser Sprache schon ein wenig Übung verloren habe und der Kontakt damit nicht so konstant ist wie Englisch).",
              img:"assets/dev3.svg"
          },

    ];

    const handleClick = (way) => {
        way === "left"
          ? setCurrentSlide(currentSlide > 0 ? currentSlide - 1 : 2)
          : setCurrentSlide(currentSlide < data.length - 1 ? currentSlide + 1 : 0);
      };


    return (
        <div className="works" id="works">
        <div
          className="slider"  style={{transform:`translateX(-${currentSlide * 100}vw)`}}>
          {data.map((d) => (
            <div className="container">
              <div className="item">
                <div className="left">
                  <div className="leftContainer">
               
                    <h2>{d.title}</h2>
                    <p>{d.desc}</p>
                    
                  </div>
                </div>
                <div className="right">
                  <img
                    src={d.img}
                    alt=""
                  />
                </div>
              </div>
            </div>
          ))}
        </div>
        <img src="assets/right-arrow.png" className="arrow left" onClick={()=>handleClick("left")}/>
        <img src="assets/right-arrow.png" className="arrow right" onClick={()=>handleClick()}/>
        
      </div>
    );
  }